import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ChartModule } from 'angular-highcharts';
import { HttpModule } from '@angular/http';

import { DashboardRoutingModule } from './dashboard-routing.module';
import { DashboardHomeComponent } from './dashboard-home/dashboard-home.component';
import { SlaMetricComponent } from './sla-metric/sla-metric.component';
import { HeaderComponent } from './header/header.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { DashboardViewComponent } from './dashboard-view/dashboard-view.component';
import { StickerComponent } from './sticker/sticker.component';
import { ChartComponent } from '../chart/chart.component';
import { DashboardHeaderModule } from '@ama/dashboard-header';
import { StickerModule } from '@ama/sticker';
import { AMAGridModule } from '@ama/grid';
import { DemoComponent } from './demo/demo.component';
import { StoreDataService } from '../services/store-data.service';
import { WebsocketService } from '../services/websocket.service';
import { ChatService } from '../services/chat.service';
import { ConfigService } from './sla-metric/config.service';
import { MetricHeaderComponent } from './metric-header/metric-header.component';
import { MetricService } from '../services/metric.service';

@NgModule({
  imports: [
    CommonModule,
    ChartModule,
    DashboardRoutingModule,
    DashboardHeaderModule,
    StickerModule,
    AMAGridModule,
    HttpModule
  ],
  declarations: [
    DashboardHomeComponent,
    SlaMetricComponent,
    HeaderComponent,
    SidebarComponent,
    DashboardViewComponent,
    StickerComponent,
    ChartComponent,
    DemoComponent,
    MetricHeaderComponent
  ],
  providers: [
    StoreDataService,
    WebsocketService,
    ChatService,
    ConfigService,
    MetricService
  ]
})
export class DashboardModule { }



