import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import {
  Router, Resolve,
  ActivatedRouteSnapshot
} from '@angular/router';

@Injectable()
export class ConfigService implements Resolve<any> {

  _configUrl = '../../../assets/data/dashboard-configs/slaMetricDashboardConfig.json';

  constructor(
    private http: Http,
    private router: Router
  ) { }

  resolve(route: ActivatedRouteSnapshot): Promise<any> | boolean {
    return this.getConfig.then(data => {
        if (data) {
            return data;
        } else { // id not found
            this.router.navigate(['/dashboard']);
            return false;
        }
    });
  }

  get getConfig(): Promise<Object> {
    return this.http.get(this._configUrl)
      .toPromise()
      .then((response) => {
        return response.json();
      })
      .catch((err) => {
        console.log(err);
      });
  }

}



