import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';
import { MetricService } from '../../services/metric.service';
import { ɵe as AMAGridService } from '@ama/grid';

@Component({
  selector: 'app-sla-metric',
  templateUrl: './sla-metric.component.html',
  styleUrls: ['./sla-metric.component.css'],
  providers: [
    AMAGridService
  ]
})
export class SlaMetricComponent implements OnInit {

  private config: any;
  pageTitle: string;
  websocketUrl: string;
  metricBlocks: any[];
  metricData: any;
  metricdeparture: any;
  metricdepartureHeaders: any;
  buubleChartData: any;

  // GRID DECLARATIONS
  public gridConfig: any = {};
  public gridRows: Array<any> = [];
  public gridColumns: Array<any> = [];
  public gridPage: Number = 1;
  public gridItemPerPage: Number = 15;
  public gridMaxSize: Number = 5;
  public gridNumPage: Number = 1;
  public gridLength: Number = 0;
  public gridLinks: any = {
    isEdit : false,
    isDelete: false
  };
  public gridMessage: any = {
      isMessage : false,
      message  : 'No Data to Display..',
      colspan : 1
  };
  public gridRowCount: number;
  public selectedGridRow: number;

  public gridTitle = 'SLA Departure Activity';
  // END OF GRID DECLARATIONS

  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private metricService: MetricService,
    private gridService: AMAGridService,
    private ref: ChangeDetectorRef
  ) {
  }

  ngOnInit() {
    this.metricBlocks = this.metricService.getMetricBlocks();
    this.buubleChartData = this.metricService.getSeries();

    this.route.data
      .subscribe((data) => {
        this.config = data.data;
      });
      console.log('Config: ', this.config);
      this.pageTitle = this.config.pageTitle;

    this.metricService.messages.subscribe(msg => {
      this.metricData = msg;
      this.metricData.metricBlock.map((obj) => {
        for ( const i in this.metricBlocks ) {
          if ( obj.title === this.metricBlocks[i].title ) {
            this.metricBlocks[i].count = obj.count;
            break;
          }
        }
      });
      this.metricdeparture.push(this.metricData.slaData);
      this.onAuditLogChangeTable(this.gridConfig);
      console.log('Response from Websocket Server: ', msg);
    });

    // Grid Configuration
    this.metricdeparture = this.metricService.getMetricDeparture();
    console.log('Departure Data: ', this.metricdeparture);
    this.metricdepartureHeaders = this.metricService.getMetricDepartureHeaders();
    this.gridService.InitializeColumns(this.metricdepartureHeaders);

    this.gridConfig = {
      heigth: 358,
      paging: true,
      sorting: {columns: this.metricdepartureHeaders},
      filtering: {filterString: ''},
      className: ['table-striped', 'table-bordered']
    };
    this.gridService.InitializeConfig(this.gridConfig);
    this.onAuditLogChangeTable(this.gridConfig);

  }

  // Table Functions
  public onAuditLogChangeTable(gridConfig: any, gridPage: any = {page: this.gridPage, itemsPerPage: this.gridItemPerPage}): any {
    this.onchange(gridConfig, gridPage, this.metricdeparture);
    this.ref.detectChanges();
  }
  public onchange(gridConfig: any, page: any, gridData: any) {
    console.log('gridConfig: ', gridConfig);
    console.log('headers: ', this.metricdepartureHeaders);
    console.log(' onChange app ---> ', gridData);
    if (gridConfig.filtering) {
      Object.assign(this.gridConfig.filtering, gridConfig.filtering);
    }

    if (gridConfig.sorting) {
      Object.assign(this.gridConfig.sorting, gridConfig.sorting);
    }
    const filteredData = this.gridService.changeFilter(gridData, gridConfig);
    if (!filteredData.length) {
      this.gridMessage.isMessage = true;
      this.gridMessage.colspan = this.metricdepartureHeaders.length;
      this.gridLength = 0;
      this.gridRowCount = 0;
    } else {
      this.gridMessage.isMessage = false;
      this.gridLength = filteredData.length;
      this.gridRowCount = filteredData.length;
    }
    const sortedData = this.gridService.changeSort(filteredData, gridConfig);

    this.gridRows = page && gridConfig.paging ? this.gridService.changePage(page, sortedData) : sortedData;
    this.gridLength = sortedData.length;
  }

}



